.nav {
  &__link {
  position: relative;
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  letter-spacing: 2px;

  &::after {
    content: '';
    position: absolute;
    bottom: -8px;
    display: block;
    height: 1px;
    width: 100%;
    background-color: $menu-text-color;

    transition: transform $effectDuration;
    transform: scale(0);
    transform-origin: left;
  }

  &:hover::after {
    transform: scale(1);
  }
 }

  &__list {
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;
  gap: 32px;
  }
}
